<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-list>
          <div>
            <v-btn small dark color="blue-grey darken-2" @click="getLocation"></v-btn>
            <template v-if="locationInfo">
              <v-btn small dark color="blue-grey darken-2" @click="openLocation"></v-btn>
            </template>
            <template v-else>
              <v-btn small :disabled="true" color="blue-grey darken-2" @click="openLocation"></v-btn>
            </template>
          </div>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      locationInfo: null
    };
  },
  methods: {
    getLocation() {
      mtl.getLocation({
        type: "wgs84",
        success: res => {
          this.locationInfo = res;
          var latitude = res.latitude; // 90 ~ -90
          var longitude = res.longitude; // 180 ~ -180
          alert(JSON.stringify(res));
        },
        fail: function(err) {
          alert(JSON.stringify(err));
        }
      });
    },
    openLocation() {
      mtl.openLocation({
        latitude: this.locationInfo.latitude,
        longitude: this.locationInfo.longitude,
        name: "",
        address: "",
        scale: 1,
        infoUrl: ""
      });
    }
  }
};
</script>