<template>
  <v-card color="grey lighten-4" flat>
    <v-toolbar flat dense>
      <v-menu :nudge-width="120">
        <template v-slot:activator="{ on }">
          <v-toolbar-title class="body-2" v-on="on">{{currentItem.title}}</v-toolbar-title>
          <v-icon medium>arrow_drop_down</v-icon>
          <v-spacer></v-spacer>
          <v-btn icon @click="$emit('add')">
            <v-icon medium>add</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-tile v-for="item in items" :key="item.title" @click="selectMenu(item)">
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="desserts"
      item-key="clientIdentify"
      class="elevation-1"
      :select-all="isSelectable"
    >
      <template v-slot:items="props">
        <td v-if="isSelectable">
          <v-checkbox v-model="props.selected" color="primary" hide-details></v-checkbox>
        </td>
        <td>{{ props.item.deviceName }}</td>
        <td>
          <v-layout justify-center>{{ props.item.deviceModel }}</v-layout>
        </td>
        <td>
          <v-layout justify-center>{{ props.item.appId }}</v-layout>
        </td>
        <td>
          <v-layout justify-center>{{ props.item.online }}</v-layout>
        </td>
        <td>
          <v-layout justify-center>
            <v-btn flat small color="error" @click="removeDevice(props.item)"></v-btn>
          </v-layout>
        </td>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
export default {
  props: {
    isSelectable: Boolean
  },
  data: () => ({
    currentItem: {
      type: "all",
      title: ""
    },
    items: [
      {
        type: "all",
        title: ""
      },
      {
        type: "authorized",
        title: ""
      },
      {
        type: "accessible",
        title: ""
      },
      {
        type: "my",
        title: ""
      }
    ],
    authorizedDevices: [],
    accessibleDevices: [],
    myDevices: [],
    selected: [],
    headers: [
      {
        text: "",
        align: "left",
        sortable: false,
        value: "deviceName"
      },
      {
        text: "",
        align: "center",
        sortable: false,
        value: "deviceModel"
      },
      {
        text: "",
        align: "center",
        sortable: false,
        value: "appId"
      },
      {
        text: "",
        align: "center",
        sortable: false,
        value: "online"
      },
      {
        text: "",
        align: "center",
        sortable: false,
        value: "remove"
      }
    ]
  }),
  computed: {
    desserts() {
      if (this.currentItem.type == "all") {
        return this.authorizedDevices
          .concat(this.accessibleDevices)
          .concat(this.myDevices);
      }
      if (this.currentItem.type == "authorized") {
        return this.authorizedDevices;
      }
      if (this.currentItem.type == "accessible") {
        return this.accessibleDevices;
      }
      if (this.currentItem.type == "my") {
        return this.myDevices;
      }
    }
  },
  watch: {
    selected: function(val) {
      this.$emit("valuechange", val);
    }
  },
  created() {
    mtl.coop.getMyDevices({
      success: res => {
        let { userId } = mtl.coop.getUserInfo();
        this.myDevices = res.map(item => {
          item.userId = userId;
          return item;
        });
      },
      fail: this.alertError
    });
    mtl.coop.getAuthorizedDevices({
      success: res => {
        this.authorizedDevices = res;
      },
      fail: this.alertError
    });
    mtl.coop.getAccessibleDevices({
      success: res => {
        this.accessibleDevices = res;
      },
      fail: this.alertError
    });
  },
  methods: {
    selectMenu(item) {
      this.currentItem = item;

      let type =
        item.type == "all" ? ["authorized", "accessible", "my"] : [item.type];
      if (type.includes("authorized")) {
        mtl.coop.getAuthorizedDevices({
          success: res => {
            this.authorizedDevices = res;
          },
          fail: this.alertError
        });
      }
      if (type.includes("accessible")) {
        mtl.coop.getAccessibleDevices({
          success: res => {
            this.accessibleDevices = res;
          },
          fail: this.alertError
        });
      }
      if (type.includes("my")) {
        mtl.coop.getMyDevices({
          success: res => {
            let { userId } = mtl.coop.getUserInfo();
            this.myDevices = res.map(item => {
              item.userId = userId;
              return item;
            });
          },
          fail: this.alertError
        });
      }
    },
    alertError(err) {
      alert(err.errMsg);
    },
    removeDevice(item) {
      let type = this.currentItem.type;
      let filter = obj => {
        return (
          obj.clientIdentify == item.clientIdentify &&
          obj.appId == item.appId &&
          obj.userId == item.userId
        );
      };
      if (type == "all") {
        if (this.myDevices.find(filter)) {
          type = "my";
        } else if (this.accessibleDevices.find(filter)) {
          type = "accessible";
        } else if (this.authorizedDevices.find(filter)) {
          type = "authorized";
        } else {
          return;
        }
      }
      if (type == "authorized") {
        mtl.coop.removeAuthorizedDevice({
          ...item,
          success: () => {
            let idx = this.authorizedDevices.findIndex(filter);
            alert(idx)
            this.authorizedDevices.splice(idx, 1);
          },
          fail: err => {
            alert(err.errMsg)
          }
        });
      } else if (type == "accessible") {
        mtl.coop.removeAccessibleDevice({
          ...item,
          success: () => {
            let idx = this.accessibleDevices.findIndex(filter);
            this.accessibleDevices.splice(idx, 1);
          },
          fail: err => {
            alert(err.errMsg)
          }
        });
      } else if (type == "my") {
        mtl.coop.removeMyDevice({
          ...item,
          success: () => {
            let idx = this.myDevices.findIndex(filter);
            this.myDevices.splice(idx, 1);
          },
          fail: err => {
            alert(err.errMsg)
          }
        });
      }
    }
  }
};
</script>