<template >
  <v-container grid-list-md pa-0>
    <v-layout column>
      <v-flex>
        <v-card>
          <CoopDevice
            :isSelectable="true"
            @valuechange="onSelectedChanged"
            @add="$emit('scanQRCode')"
          />
          <v-flex>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" :disabled="sendBtnDisabled" @click="sendCommand"></v-btn>
            </v-card-actions>
          </v-flex>
        </v-card>
      </v-flex>
      <v-flex>
        <v-card>
          <v-toolbar flat dense>
            <v-toolbar-title class="body-2"></v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn v-if="switch1" flat color="primary" @click="$emit('showQRCode')"></v-btn>
            <span :style="'margin-top:20px;'">
              <v-switch label="" color="primary" v-model="switch1"></v-switch>
            </span>
          </v-toolbar>
          <v-data-table
            v-if="switch1"
            :headers="headers1"
            :items="accessRequests"
            item-key="clientIdentify"
            class="elevation-1"
          >
            <template v-slot:items="props">
              <td>
                <v-layout
                  style="line-height:16px;max-height:32px;overflow:hidden;"
                  justify-center
                >{{ props.item.deviceInfo.clientIdentify }}</v-layout>
              </td>
              <td>
                <v-layout
                  style="line-height:16px;max-height:32px;overflow:hidden;"
                  justify-center
                >{{ props.item.deviceInfo.userId }}</v-layout>
              </td>
              <td>
                <v-layout
                  style="line-height:16px;max-height:32px;overflow:hidden;"
                  justify-center
                >{{ props.item.deviceInfo.appId }}</v-layout>
              </td>
              <td>
                <v-layout justify-center>
                  <v-btn flat small color="success" @click="agree(props.item.commandId)"></v-btn>
                  <v-btn flat small color="error" @click="reject(props.item.commandId)"></v-btn>
                </v-layout>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <v-flex>
        <v-card>
          <v-toolbar flat dense>
            <v-toolbar-title class="body-2"></v-toolbar-title>
            <v-spacer></v-spacer>
            <span :style="'margin-top:20px;'">
              <v-switch label="" color="primary" v-model="switch2"></v-switch>
            </span>
          </v-toolbar>
          <v-data-table
            v-if="switch2"
            :headers="headers2"
            :items="receivedCommands"
            item-key="clientIdentify"
            class="elevation-1"
          >
            <template v-slot:items="props">
              <tr @click="props.expanded = !props.expanded">
                <td>
                  <v-layout
                    style="line-height:16px;max-height:32px;overflow:hidden;"
                    justify-center
                  >{{ props.item.from.clientIdentify }}</v-layout>
                </td>
                <td>
                  <v-layout justify-center>{{ props.item.from.appId }}</v-layout>
                </td>
                <td>
                  <v-layout justify-center>{{ props.item.from.userId }}</v-layout>
                </td>
                <td>
                  <v-layout justify-center>
                    <v-btn flat small color="info" @click="replyCmd(props.item)"></v-btn>
                    <v-btn flat small color="error" @click="deleteCmd(props.index)"></v-btn>
                  </v-layout>
                </td>
              </tr>
            </template>
            <template v-slot:expand="props">
              <v-card flat color="#2d2c2c" class="white--text">
                <v-card-title primary-title>
                  <div>
                    <span>:</span>
                    <br>
                    <span>{{JSON.stringify(props.item, undefined, 2)}}</span>
                  </div>
                </v-card-title>
              </v-card>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import CoopDevice from "./Coop.Device";

export default {
  components: {
    CoopDevice
  },
  methods: {
    onSelectedChanged(devices) {
      this.selectedDevices = devices;
      this.sendBtnDisabled = devices.length == 0;
    },
    agree(commandId) {
      let items = this.accessRequests;
      let index = items.findIndex(item => item.commandId == commandId);
      let cmd = items[index];
      items.splice(index, 1);
      this.accessRequests = items;
      let { deviceInfo: to, commandParams } = cmd;
      mtl.coop.agreeAccessRequest({
        commandId,
        to,
        commandParams,
        success: () => {},
        fail: err => {
          alert(err.errMsg);
        }
      });
    },
    reject(commandId) {
      let items = this.accessRequests;
      let index = items.findIndex(item => item.commandId == commandId);
      let cmd = items[index];
      items.splice(index, 1);
      this.accessRequests = items;
      let { deviceInfo: to, commandParams } = cmd;
      mtl.coop.rejectAccessRequest({
        commandId,
        to,
        commandParams,
        success: res => {},
        fail: err => {
          alert(err.errMsg);
        }
      });
    },
    sendCommand() {
      for (let device of this.selectedDevices) {
        mtl.coop.sendCommand({
          commandType: "CUSTOM_TYPE",
          to: device,
          commandParams: {},
          receiveReply: function(res) {
            alert(":" + JSON.stringify(res));
          }
        });
      }
    },
    replyCmd(cmd) {
      let _cmd = { ...cmd };
      _cmd.to = _cmd.from;
      _cmd.commandParams = {
        msg: ""
      };
      delete _cmd.from;
      delete _cmd.commandType;
      delete _cmd.response;
      _cmd.fail = err => {
        alert(err.errMsg);
      };
      mtl.coop.replyCommand(_cmd);
    },
    deleteCmd(index) {
      let commands = this.receivedCommands;
      this.receivedCommands = commands.filter((obj, idx) => idx != index);
    }
  },
  data() {
    return {
      selectedDevices: [],
      sendBtnDisabled: true,
      switch1: false,
      switch2: false,
      headers1: [
        {
          text: " ID",
          align: "center",
          sortable: false,
          value: "clientIdentify"
        },
        {
          text: "",
          align: "center",
          sortable: false,
          value: "appId"
        },
        {
          text: "",
          align: "center",
          sortable: false,
          value: "userId"
        },
        {
          text: "",
          align: "center",
          sortable: false
        }
      ],
      headers2: [
        {
          text: "ID",
          align: "center",
          sortable: false,
          value: "clientIdentify"
        },
        {
          text: "",
          align: "center",
          sortable: false,
          value: "appId"
        },
        {
          text: "",
          align: "center",
          sortable: false,
          value: "userId"
        },
        {
          text: "",
          align: "center",
          sortable: false
        }
      ],
      accessRequests: [],
      receivedCommands: []
    };
  },
  watch: {
    switch1(val) {
      window.switch1_this = this;
      mtl.coop.receiveAccessRequest(function(res) {
        if (!val) {
          return;
        }
        let { deviceInfo, commandId, commandParams } = res;
        var origin = window.switch1_this.accessRequests;
        window.switch1_this.accessRequests = origin.concat({
          deviceInfo: res.from,
          commandId,
          commandParams
        });
      });
    },
    switch2(val) {
      mtl.coop.receiveCommand(cmd => {
        alert(JSON.stringify(cmd));
        let commands = this.receivedCommands;
        this.receivedCommands = [cmd].concat(commands);
      });
    }
  }
};
</script>