<template>
  <v-layout>
    <v-flex>
      <v-card ref="form">
        <v-card-text>
          <v-text-field
            ref="server"
            v-model="server"
            :rules="[() => !!server || 'This field is required']"
            :error-messages="errorMessages"
            label="server"
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            ref="restServer"
            v-model="restServer"
            :rules="[() => !!restServer || 'This field is required']"
            label="restServer"
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            ref="resourceUploadServer"
            v-model="resourceUploadServer"
            :rules="[() => !!resourceUploadServer || 'This field is required']"
            label="resourceUploadServer"
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            ref="resourceDownloadServer"
            v-model="resourceDownloadServer"
            :rules="[() => !!resourceDownloadServer || 'This field is required']"
            label="resourceDownloadServer"
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            ref="serverPort"
            v-model="serverPort"
            :rules="[() => !!serverPort || 'This field is required']"
            label="serverPort"
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            ref="serverSSLPort"
            v-model="serverSSLPort"
            :rules="[() => !!serverSSLPort || 'This field is required']"
            label="serverSSLPort"
            placeholder="SSL "
            required
          ></v-text-field>
          <v-autocomplete
            ref="serverEnableSSL"
            v-model="serverEnableSSL"
            :rules="[() => serverEnableSSL != null || 'This field is required']"
            :items="[{text: '', value: true}, {text: '', value: false}]"
            label="serverEnableSSL"
            placeholder=" SSL "
            required
          ></v-autocomplete>
          <v-autocomplete
            ref="serverHttps"
            v-model="serverHttps"
            :rules="[() => serverHttps != null || 'This field is required']"
            :items="[{text: '', value: true}, {text: '', value: false}]"
            label="serverHttps"
            placeholder=" HTTPS"
            required
          ></v-autocomplete>
          <v-text-field
            ref="appId"
            v-model="appId"
            :rules="[() => !!appId || 'This field is required']"
            label="appId"
            placeholder=" ID"
            required
          ></v-text-field>
          <v-text-field
            ref="etpId"
            v-model="etpId"
            :rules="[() => !!etpId || 'This field is required']"
            label="etpId"
            placeholder=" ID"
            required
          ></v-text-field>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn flat @click="resetForm"></v-btn>
          <v-spacer></v-spacer>
          <v-btn :disabled="submitDisabled" color="primary" flat @click="submit"></v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data: () => ({
    submitDisabled: true,
    formHasErrors: false,
    errorMessages: "",
    server: null,
    restServer: null,
    resourceUploadServer: null,
    resourceDownloadServer: null,
    serverPort: null,
    serverSSLPort: null,
    serverEnableSSL: null,
    serverHttps: null,
    appId: null,
    etpId: null
  }),

  computed: {
    form() {
      return {
        server: this.server,
        restServer: this.restServer,
        resourceUploadServer: this.resourceUploadServer,
        resourceDownloadServer: this.resourceDownloadServer,
        serverPort: this.serverPort,
        serverSSLPort: this.serverSSLPort,
        serverEnableSSL: this.serverEnableSSL,
        serverHttps: this.serverHttps,
        appId: this.appId,
        etpId: this.etpId
      };
    }
  },

  watch: {
    form: {
      handler: function(val) {
        this.submitDisabled = false;
      },
      deep: true
    }
  },

  methods: {
    resetForm() {
      let defaultSettings = {
        server: "im-pre.yyuap.com", //
        restServer: "im-pre.yyuap.com", //
        resourceUploadServer: "im-pre.yyuap.com", //
        resourceDownloadServer: "im-pre.yyuap.com", //
        serverPort: 5227, //
        serverSSLPort: 5223, //SSL
        serverEnableSSL: true, //SSL
        serverHttps: true, //https
        appId: "mttest02", //ID
        etpId: "yonyou" //ID
      };

      this.errorMessages = [];
      this.formHasErrors = false;

      Object.keys(defaultSettings).forEach(f => {
        this[f] = defaultSettings[f];
      });
    },
    submit() {
      this.formHasErrors = false;
      Object.keys(this.form).forEach(f => {
        if (!this.form[f]) this.formHasErrors = true;
        this.$refs[f].validate(true);
      });
      if (!this.formHasErrors) {
        this.submitDisabled = true;
        this.$emit("submit", this.form);
      }
    }
  }
};
</script>