<template>
  <v-dialog v-model="show" max-width="300">
    <v-card class="pa-4">
      <v-card>
        <v-img v-if="imgsrc" :src="imgsrc" style="min-width:200px;min-height:200px;" aspect-ratio="1" class="grey lighten-2" contain/>
      </v-card>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  model: {
    prop: "show",
    event: "close"
  },
  props: {
    show: Boolean,
    imgsrc: String
  },
  data() {
    return {};
  },
  watch: {
    show() {
      this.$emit("close", this.show);
    }
  }
};
</script>