<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-card-title v-if="imgSrc">
          <v-img :src="imgSrc"/>
        </v-card-title>
        <v-list subheader>
          <v-subheader v-if="!imgSrc"></v-subheader>
          <div>
            <v-btn small dark color="blue-grey darken-2" @click="chooseImage"></v-btn>
          </div>
        </v-list>
        <v-list v-if="imgSrc" subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <v-btn small dark color="blue-grey darken-2" @click="uploadImage"></v-btn>
            <v-btn small dark color="blue-grey darken-2" @click="downloadImage"></v-btn>
          </div>
        </v-list>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <v-btn small dark color="blue-grey darken-2" @click="previewImage"></v-btn>
          </div>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { platform } from "os";
export default {
  data() {
    return {
      localId: null,
      imgSrc: null
    };
  },
  methods: {
    chooseImage() {
      mtl.chooseImage({
        count: 1,
        sizeType: ["original", "compressed"],
        sourceType: ["album", "camera"],
        success: res => {
          var localIds = res.localIds;
          let _localId = localIds[0];
          if (mtl.platform.name == "upesn") {
            this.imgSrc = _localId;
          } else {
            mtl.getLocalImgData({
              localId: _localId,
              success: res => {
                let localData = res.localData;
                this.imgSrc =
                  (localData.startsWith("data:")
                    ? ""
                    : "data:image/png;base64,") + localData;
              }
            });
          }
          this.localId = _localId;
        }
      });
    },
    previewImage() {
      mtl.previewImage({
        current:
          "https://wx2.sinaimg.cn/mw690/70396e5agy1g0leayhrhoj211418gq9q.jpg",
        urls: [
          "https://wx3.sinaimg.cn/mw690/70396e5agy1g0leax4owvj211418gk2j.jpg",
          "https://wx4.sinaimg.cn/mw690/70396e5agy1g0leay2ymtj211418gk11.jpg",
          "https://wx2.sinaimg.cn/mw690/70396e5agy1g0leayhrhoj211418gq9q.jpg",
          "https://wx3.sinaimg.cn/mw690/70396e5agy1g0leazeikxj211418gqdc.jpg"
        ]
      });
    },
    uploadImage() {
      mtl.uploadImage({
        localId: this.localId,
        isShowProgressTips: 1,
        success: res => {
          this.serverId = res.serverId;
          alert("!");
        }
      });
    },
    downloadImage() {
      mtl.downloadImage({
        serverId: this.serverId,
        isShowProgressTips: 1,
        success: function(res) {
          var localId = res.localId;
          mtl.getLocalImgData({
            localId: localId,
            success: res => {
              let localData = res.localData;
              this.imgSrc =
                (localData.startsWith("data:")
                  ? ""
                  : "data:image/png;base64,") + localData;
            }
          });
          this.localId = localId;
        }
      });
    }
  }
};
</script>