<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-card-title v-if="imgSrc">
          <v-img :src="imgSrc"/>
        </v-card-title>
        <v-list>
          <div>
            <v-btn small dark color="blue-grey darken-2" @click="generateQRCode"></v-btn>
            <v-btn small dark color="blue-grey darken-2" @click="scanQRCode"></v-btn>
          </div>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      imgSrc: null
    };
  },
  methods: {
    generateQRCode() {
      mtl.generateQRCode({
        str: "QRCode:123456",
        success: res => {
          let localData = res.imgSrc;
          this.imgSrc =
            (localData.startsWith("data:") ? "" : "data:image/png;base64,") +
            localData;
        }
      });
    },
    scanQRCode() {
      mtl.scanQRCode({
        needResult: 1,
        scanType: ["qrCode", "barCode"],
        success: res => {
          var result = res.resultStr;
          alert(":" + result);
        }
      });
    }
  }
};
</script>