<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <template v-if="recording">
              <v-btn :disabled="true" small color="blue-grey darken-2" @click="startRecord"></v-btn>
              <v-btn :disabled="false" small dark color="blue-grey darken-2" @click="stopRecord"></v-btn>
            </template>
            <template v-else>
              <v-btn :disabled="false" small dark color="blue-grey darken-2" @click="startRecord"></v-btn>
              <v-btn :disabled="true" small color="blue-grey darken-2" @click="stopRecord"></v-btn>
            </template>
            <template v-if="recordListening">
              <v-btn small dark color="primary" @click="onVoiceRecordEnd">()</v-btn>
            </template>
            <template v-else>
              <v-btn small @click="onVoiceRecordEnd">()</v-btn>
            </template>
          </div>
        </v-list>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <template v-if="playing">
              <template v-if="pause">
                <v-btn small dark color="blue-grey darken-2" @click="playVoice"></v-btn>
                <v-btn :disabled="true" small color="blue-grey darken-2" @click="pauseVoice"></v-btn>
              </template>
              <template v-else>
                <v-btn :disabled="true" small color="blue-grey darken-2" @click="playVoice"></v-btn>
                <v-btn small dark color="blue-grey darken-2" @click="pauseVoice"></v-btn>
              </template>
              <v-btn small dark color="blue-grey darken-2" @click="stopVoice"></v-btn>
            </template>
            <template v-else>
              <v-btn small dark color="blue-grey darken-2" @click="playVoice"></v-btn>
              <v-btn :disabled="true" small color="blue-grey darken-2" @click="pauseVoice"></v-btn>
              <v-btn :disabled="true" small color="blue-grey darken-2" @click="stopVoice"></v-btn>
            </template>
            <template v-if="playListening">
              <v-btn small dark color="primary" @click="onVoicePlayEnd">()</v-btn>
            </template>
            <template v-else>
              <v-btn small @click="onVoicePlayEnd">()</v-btn>
            </template>
          </div>
        </v-list>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <template v-if="localId">
              <v-btn small dark color="blue-grey darken-2" @click="uploadVoice"></v-btn>
            </template>
            <template v-else>
              <v-btn small :disabled="true" color="blue-grey darken-2" @click="uploadVoice"></v-btn>
            </template>
            <template v-if="serverId">
              <v-btn small dark color="blue-grey darken-2" @click="downloadVoice"></v-btn>
            </template>
            <template v-else>
              <v-btn small :disabled="true" color="blue-grey darken-2" @click="downloadVoice"></v-btn>
            </template>
          </div>
        </v-list>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <template v-if="localId">
              <v-btn small dark color="blue-grey darken-2" @click="translateVoice"></v-btn>
            </template>
            <template v-else>
              <v-btn small :disabled="true" color="blue-grey darken-2" @click="translateVoice"></v-btn>
            </template>
          </div>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      recording: false,
      recordListening: false,
      playing: false,
      pause: false,
      playListening: false,
      localId: null,
      serverId: null
    };
  },
  methods: {
    startRecord() {
      this.recording = true;
      mtl.startRecord();
    },
    stopRecord() {
      this.recording = false;
      mtl.stopRecord({
        success: res => {
          this.localId = res.localId;
        },
        fail: err => {
          alert(err.errMsg)
        }
      });
    },
    onVoiceRecordEnd() {
      this.recordListening = !this.recordListening;
      mtl.onVoiceRecordEnd({
        complete: res => {
          this.localId = res.localId;
          this.recording = false;
        }
      });
    },
    playVoice() {
      this.playing = true;
      this.pause = false;
      mtl.playVoice({
        localId: this.localId
      });
    },
    pauseVoice() {
      this.pause = true;
      mtl.pauseVoice({
        localId: this.localId
      });
    },
    stopVoice() {
      this.playing = false;
      this.pause = false;
      mtl.stopVoice({
        localId: this.localId
      });
    },
    onVoicePlayEnd() {
      this.playListening = !this.playListening;
      mtl.onVoicePlayEnd({
        success: res => {
          if (this.playListening) {
            var localId = res.localId;
            this.playing = false;
            this.pause = false;
          }
        },
        fail: err => {
          alert(err.errMsg)
        }
      });
    },
    uploadVoice() {
      mtl.uploadVoice({
        localId: this.localId,
        isShowProgressTips: 1,
        success: res => {
          this.serverId = res.serverId;
          alert(", serverId:" + this.serverId);
        },
        fail: err => {
          alert(err.errMsg)
        }
      });
    },
    downloadVoice() {
      mtl.downloadVoice({
        serverId: this.serverId,
        isShowProgressTips: 1,
        success: res => {
          this.localId = res.localId;
          alert("" + this.localId);
        },
        fail: err => {
          alert(err.errMsg)
        }
      });
    },
    translateVoice: function() {
      mtl.translateVoice({
        localId: this.localId,
        isShowProgressTips: 1,
        success: function(res) {
          alert(res.translateResult);
        },
        fail: err => {
          alert(err.errMsg)
        }
      });
    }
  }
};
</script>