<template>
  <v-layout>
    <v-flex>
      <v-card ref="form">
        <v-card-text>
          <v-text-field
            ref="userId"
            v-model="userId"
            :rules="[() => !!userId || 'This field is required']"
            label="userId"
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            v-model="password"
            ref="password"
            :rules="[() => !!password || 'This field is required']"
            label="password"
            placeholder=""
            required
            :append-icon="visibility ? 'visibility' : 'visibility_off'"
            :type="visibility ? 'text' : 'password'"
            @click:append="visibility = !visibility"
          ></v-text-field>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn flat @click="resetForm"></v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="submit"></v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      visibility: false,
      userId: null,
      password: null
    };
  },
  computed: {
    form() {
      return {
        userId: this.userId,
        password: this.password
      };
    }
  },
  methods: {
    resetForm() {
      this.errorMessages = [];
      this.formHasErrors = false;

      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
    },
    submit() {
      this.formHasErrors = false;
      Object.keys(this.form).forEach(f => {
        if (!this.form[f]) this.formHasErrors = true;
        this.$refs[f].validate(true);
      });
      if (!this.formHasErrors) {
        this.$emit("submit", this.form);
      }
    }
  }
};
</script>