<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <v-btn
              small
              dark
              color="blue-grey darken-2"
              @click="chooseImage(0)"
            >({{image0?'':''}})</v-btn>
            <v-btn small dark color="blue-grey darken-2" @click="recognize(0)"></v-btn>
          </div>
        </v-list>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <v-btn
              small
              dark
              color="blue-grey darken-2"
              @click="chooseImage(1)"
            >({{image1?'':''}})</v-btn>
            <v-btn small dark color="blue-grey darken-2" @click="recognize(1, 'face')">()</v-btn>
            <v-btn small dark color="blue-grey darken-2" @click="recognize(1, 'back')">()</v-btn>
          </div>
        </v-list>
        <v-list subheader>
          <v-subheader :style="'height:36px;'"></v-subheader>
          <div>
            <v-btn
              small
              dark
              color="blue-grey darken-2"
              @click="chooseImage(2)"
            >({{image2?'':''}})</v-btn>
            <v-btn small dark color="blue-grey darken-2" @click="recognize(2)"></v-btn>
          </div>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      image0: null,
      image1: null,
      image2: null,
      appcode: "397a546045454397bfa68c918df3bb18"
    };
  },
  methods: {
    chooseImage(type) {
      let image = this["image" + type];
      if (image) {
        this["image" + type] = null;
      } else {
        mtl.chooseImage({
          count: 1,
          sizeType: ["original", "compressed"],
          sourceType: ["album", "camera"],
          crop: true,
          maxWidth: 3000,
          maxHeight: 3000,
          quality: 90,
          success: res => {
            var localIds = res.localIds;
            this["image" + type] = localIds[0];
          },
          fail: err => {
            alert(JSON.stringify(err));
          }
        });
      }
    },
    recognize(type, side) {
      let image = this["image" + type];
      switch (type) {
        case 0:
          if (image) {
            this.recognizeInvoice(image);
          } else {
            this.scanInvoice();
          }
          break;
        case 1:
          if (image) {
            this.recognizeIDCard(image, side);
          } else {
            this.scanIDCard(side);
          }
          break;
        case 2:
          if (image) {
            this.recognizeBankCard(image);
          } else {
            this.scanBankCard();
          }
          break;
        default:
          break;
      }
    },
    scanInvoice() {
      mtl.scanInvoice({
        appCode: this.appcode,
        success: function(res) {
          var result = res.data;
          var resultStr = JSON.stringify(result);
          if (resultStr.length > 200) {
            resultStr = resultStr.substring(0, 200);
          }
          alert(resultStr);
        },
        fail: err => {
          alert(JSON.stringify(err));
        }
      });
    },
    recognizeInvoice(image) {
      mtl.recognizeInvoice({
        appCode: this.appcode,
        image: image,
        success: function(res) {
          var result = res.data;
          var resultStr = JSON.stringify(result);
          if (resultStr.length > 200) {
            resultStr = resultStr.substring(0, 200);
          }
          alert(resultStr);
        },
        fail: err => {
          alert(JSON.stringify(err));
        }
      });
    },
    scanIDCard(side) {
      mtl.scanIDCard({
        appCode: this.appcode,
        side: side,
        success: function(res) {
          var result = res.data;
          alert(JSON.stringify(result));
        },
        fail: err => {
          alert(JSON.stringify(err));
        }
      });
    },
    recognizeIDCard(image, side) {
      mtl.recognizeIDCard({
        appCode: this.appcode,
        side: side,
        image: image,
        success: function(res) {
          var result = res.data;
          alert(JSON.stringify(result));
        },
        fail: err => {
          alert(JSON.stringify(err));
        }
      });
    },
    scanBankCard() {
      mtl.scanBankCard({
        appCode: this.appcode,
        success: function(res) {
          var result = res.data;
          alert(JSON.stringify(result));
        },
        fail: err => {
          alert(JSON.stringify(err));
        }
      });
    },
    recognizeBankCard(image) {
      mtl.recognizeBankCard({
        appCode: this.appcode,
        image: image,
        success: function(res) {
          var result = res.data;
          alert(JSON.stringify(result));
        },
        fail: err => {
          alert(JSON.stringify(err));
        }
      });
    }
  }
};
</script>