<template>
  <div v-if="step == 3">
    <CoopQRCode v-model="dialog" :imgsrc="qrcodeImg"/>
    <CoopHome @scanQRCode="scanQRCode" @showQRCode="showQRCode"/>
  </div>
  <div v-else>
    <v-stepper v-model="step">
      <v-stepper-header>
        <v-stepper-step :complete="step > 1" step="1"></v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="step > 2" step="2"></v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card class="mb-4">
            <CoopInit @submit="init"/>
          </v-card>
          <v-btn :disabled="!nextEnable" color="primary" @click="step = 2; nextEnable = false;"></v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card class="mb-4">
            <CoopLogin @submit="login"/>
          </v-card>
          <v-btn :disabled="!nextEnable" color="primary" @click="step = 3; nextEnable = false;"></v-btn>
          <v-btn flat @click="step = step - 1"></v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>

<script>
import CoopInit from "./Coop.Init";
import CoopLogin from "./Coop.Login";
import CoopDevice from "./Coop.Device";
import CoopHome from "./Coop.Home";
import CoopQRCode from "./Coop.QRCode";

export default {
  components: {
    CoopInit,
    CoopLogin,
    CoopDevice,
    CoopHome,
    CoopQRCode
  },
  beforeRouteLeave(to, from, next) {
    mtl.coop.logout({
      success: function() {
        // 
      }
    });
    this.step = 0;
    next();
  },
  methods: {
    init(form) {
      this.nextEnable = true;
      mtl.coop.initialize(form);
    },
    login({ userId, password }) {
      this.nextEnable = true;
      mtl.coop.login({
        userId,
        password,
        success: function() {
          
        },
        fail: function(err) {
          alert(err.errMsg);
        }
      });
    },
    enter() {},
    showQRCode() {
      this.dialog = true;
      mtl.coop.getOpenId({
        success: res => {
          var openId = res.openId;
          mtl.generateQRCode({
            str: openId,
            success: res => {
              let localData = res.imgSrc;
              this.qrcodeImg =
                (localData.startsWith("data:")
                  ? ""
                  : "data:image/png;base64,") + localData;
              this.dialog = true;
            },
            fail: err => {
              alert(err.errMsg);
            }
          });
        },
        fail: err => {
          alert(err.errMsg);
        }
      });
    },
    scanQRCode() {
      mtl.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: res => {
          var openId = res.resultStr;
          mtl.coop.getDeviceInfo({
            openId: openId,
            success: res => {
              var appId = res.appId; // ID
              var permissions = res.permissions; // 
              var userId = res.userId; // ID
              var clientIdentify = res.clientIdentify; // ID
              res.allPermissions = true;
              mtl.coop.requestAccessTo({
                appId: appId,
                userId: userId,
                clientIdentify: clientIdentify,
                permissions: permissions,
                allPermissions: true,
                success: res => {
                  alert(JSON.stringify(res));
                  // var message = res.message; // 
                  // var code = res.code; // true   false
                  // var from = res.from;
                  // var clientIdentify = from.clientIdentify; // ID
                  // var appId = from.appId; // ID
                  // var userId = from.userId; // ID
                },
                fail: err => {
                  alert(err.errMsg);
                }
              });
            }
          });
        },
        fail: err => {
          alert(err.errMsg);
        }
      });
    }
  },
  data() {
    return {
      nextEnable: false,
      step: 0,
      dialog: false,
      openIdQR: null,
      qrcodeImg: null
    };
  }
};
</script>